import React, { useState } from "react";
import classNames from "classnames/bind";
import styles from "./Plan.css";

let cx = classNames.bind(styles);

function Plan(props) {
  const [selected, setSelected] = useState(false);

  const handleChange = () => {
    setSelected(!selected);
  };

  let cardColor = cx("plan", {
    lightBlue: props.color === "lightBlue",
    green: props.color === "green",
    red: props.color === "red",
    black: props.color === "black",
    selected: selected,
  });

  return (
    <div className={cardColor} selected={selected} onClick={handleChange}>
      <div className={styles.header}> {props.price}</div>
      <div className={styles.bodyTop}>
         <span>{props.price}</span> /
      </div>
      <div className={styles.bodyBottom}>
         <span>{props.speed}</span> /
      </div>
      <div className={styles.footer}>
            
      </div>
    </div>
  );
}

export default Plan;
